let Utils = {
	/**
	 * æ¯å¦ä¸ºé«å¹´
	 * @return {Boolse} true|false
	 */
	isLeapYear: function(y) {
	    return (y % 4 == 0 && y % 100 != 0) || y % 400 == 0;
	},

	/**
	 * è¿åæææ°
	 * @return {Number}
	 */
	getWhatDay: function(year, month, day) {
	    let date = new Date(year + '/' + month + '/' + day);
		let index = date.getDay();
		let dayNames = ['æææ¥', 'ææä¸', 'ææäº', 'ææä¸', 'ææå', 'ææäº', 'ææå­'];
	    return dayNames[index];
	},

	/**
     * è¿åæææ°
     * @return {Number}
     */
    getMonthPreDay: function(year, month) {
        let date = new Date(year + '/' + month + '/01');
        let day = date.getDay();
        if (day == 0) {
            day = 7;
        }
        return day;
    },

	/**
	 * è¿åæä»½å¤©æ°
	 * @return {Number}
	 */
	getMonthDays: function(year, month) {
	    if (/^0/.test(month)) {
	        month = month.split('')[1];
	    }
	    return [0, 31, (this.isLeapYear(year) ? 29 : 28), 31, 30, 31, 30, 31, 31, 30, 31, 30, 31][month];
	},

	/**
	 * è¡¥é½æ°å­ä½æ°
	 * @return {string}
	 */
	getNumTwoBit: function(n) {
		n = Number(n);
		return (n > 9 ? '' : '0') + n;
	},

	/**
	 * æ¥æå¯¹è±¡è½¬æå­ç¬¦ä¸²
	 * @return {string}
	 */
	date2Str: function(date, split) {
	    if (typeof date == 'string') return date;
	    split = split || '-';
	    let y = date.getFullYear();
	    let m = this.getNumTwoBit(date.getMonth() + 1);
	    let d = this.getNumTwoBit(date.getDate());
	    return [y, m, d].join(split);
	},

	/**
	 * è¿åæ¥ææ ¼å¼å­ç¬¦ä¸²
	 * @param {Number} 0è¿åä»å¤©çæ¥æã1è¿åæå¤©çæ¥æï¼2è¿ååå¤©å¾æ¥æï¼ä¾æ¬¡ç±»æ¨
	 * @return {string} '2014-12-31'
	 */
	getDay: function (i) {
	    i = i || 0;
	    let date = new Date();
	    let diff = i * (1000 * 60 * 60 * 24);
	    date = new Date(date.getTime() + diff);
	    return this.date2Str(date);
	},

	/**
	 * æ¶é´æ³è½¬æ¢ä¸ºæ¥ææ ¼å¼
	 * @return {String}
	 */
	timestampToDate: function (timestamp) {
	    let date = new Date(timestamp);
	    return date.getFullYear() + '-' + getNumTwoBit((date.getMonth() + 1)) + '-' + getNumTwoBit(date.getDate());
	},

	/**
     * æ¶é´æ¯è¾
     * @return {Boolean}
     */
    compareDate: function (date1, date2) {
        let startTime = new Date(date1.replace('-', '/').replace('-', '/'));
        let endTime = new Date(date2.replace('-', '/').replace('-', '/'));
        if (startTime >= endTime) {
            return false;
        }
        return true;
	},
	/**
     * æ¶é´æ¯å¦ç¸ç­
     * @return {Boolean}
     */
    isEqual: function (date1, date2) {
        let startTime = new Date(date1).getTime();
        let endTime = new Date(date2).getTime();
        if (startTime == endTime) {
            return true;
        }
        return false;
	}
};

export default Utils;